@use '_partials/_variables' as v
@use '_partials/_mixins' as m

/* VII. Media-queries
 --------------------------------------- */

@media (max-width: 1699.98px)

// XL
@media (max-width: 1399.98px)
  html
    font-size: 9px

  .container
    max-width: 114rem

  .d
    &-none
      &_xl
        display: none

    &-block
      &_xl
        display: block

    &-inline
      &_xl
        display: inline

// LG
@media (max-width: 1199.98px)
  html
    font-size: 8px

  .container
    &,
    &_xl
      max-width: 96rem

  .d
    &-none
      &_lg
        display: none

    &-block
      &_lg
        display: block

    &-inline
      &_lg
        display: inline

// MD
@media (max-width: 991.98px)
  // html
  //   font-size: 8px

  // .btn
  //   &_primary
  //     padding-left: 2em
  //     padding-right: 2em

  //   &_secondary
  //     padding: 0 1.9em
  //     font-size: 15px

  .container
    &,
    &_xl,
    &_lg
      max-width: 740px

  .d
    &-none
      &_md
        display: none

    &-block
      &_md
        display: block

    &-inline
      &_md
        display: inline

// SM
@media (max-width: 767.98px)
  // html
  // 	font-size: 7px
  // 
  .container
    &,
    &_xl,
    &_lg,
    &_md
      max-width: 540px

  .d
    &-none
      &_sm
        display: none

    &-block
      &_sm
        display: block

    &-inline
      &_sm
        display: inline

// XS
@media (max-width: 575.98px)
  // html
  // 	font-size: 6px
  // 
  .container
    &,
    &_xl,
    &_lg,
    &_md,
    &_sm
      max-width: 440px
      padding-left: 2.5rem
      padding-right: 2.5rem

  .d
    &-none
      &_xs
        display: none

    &-block
      &_xs
        display: block

    &-inline
      &_xs
        display: inline

@media (max-width: 479.98px)

// XXS
@media (max-width: 414.98px)
  .d
    &-none
      &_xxs
        display: none

    &-block
      &_xxs
        display: block

    &-inline
      &_xxs
        display: inline

@media (max-width: 359.99px)

@media (max-width: 320px)

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none)
