	&_bold
		font-weight: 700

//  3. Links
//  -------------------------

a, a:visited
	text-decoration: none
	outline: none
	color: v.$menu-item-color
	transition: color .3s ease-in-out

	&-link
		color: v.$text-link-color
		text-decoration: underline

a

	&-link
		&:hover,
		&:focus
			text-decoration: none

		&:active
			text-decoration: none

//   4. Images
//  -------------------------

img
	display: block
	max-width: 100%
	height: auto

//   5. Buttons
//  -------------------------

button,
input[type="submit"],
input[type="reset"],
input[type="button"],
.btn
	font-family: "Raleway", "Helvetica", sans-serif
	text-decoration: none
	cursor: pointer
	user-select: none

//  .btn styles from prev version

	&
		display: inline-flex
		justify-content: center
		align-items: center
		border-radius: 8px
		background: none
		overflow: hidden
	&__text
		margin-left: 1.2rem

	&_full-width,
	&_lg,
	&_md,
	&_sm
		// заполняет всю ширину родителя- независимо от текста
		width: 100%

	&_lg
		max-width: 20rem

	&_md
		max-width: 15rem

	&_sm
		max-width: 10rem

